.section__hero {
  flex-direction: column-reverse;
  max-width: 1275px;
  display: flex;
}

@media screen and (width >= 1275px) {
  .section__hero {
    margin: unset;
    margin-left: calc((100% - var(--section-width)) / 2);
  }
}

.section__hero--text {
  padding: 0 var(--section-padding);
  flex-direction: column;
  align-items: center;
  padding-top: 2.5rem;
  padding-bottom: 5.5rem;
  display: flex;
  overflow: hidden;
}

.hero--heading {
  font-size: 2.5rem;
  font-weight: var(--fw-light);
  letter-spacing: -.044375rem;
  text-align: center;
  color: var(--dark);
  max-width: 15ch;
}

.hero--description {
  font-size: .9375rem;
  font-weight: var(--fw-medium);
  letter-spacing: -.014375rem;
  text-align: center;
  color: var(--greyish--blue);
  max-width: 27.9375rem;
  margin-top: 1rem;
  line-height: 1.5625rem;
}

.section__hero--text .requestInviteCta {
  margin-top: 2rem;
}

.section__hero--mockup {
  background-image: url("bg-intro-mobile.f75cb22f.svg");
  background-position: 0 -130px;
  background-repeat: no-repeat;
  background-size: 100%;
  width: 100%;
  height: 284px;
  position: relative;
}

.section__hero--mockup img {
  width: 327px;
  height: 402px;
  position: absolute;
  bottom: -50px;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (width >= 820px) {
  .section__hero {
    grid-template-columns: 447px 1fr;
    display: grid;
  }

  .section__hero--text {
    justify-content: center;
    align-items: start;
  }

  .hero--heading, .hero--description {
    text-align: start;
  }

  .section__hero--text .requestInviteCta {
    margin-top: 2.25rem;
  }

  .section__hero--mockup {
    max-height: unset;
    background-image: url("bg-intro-desktop.63e3f11d.svg");
    background-position: 0 -260px;
    background-size: auto;
    height: 656px;
    overflow-x: clip;
  }

  .section__hero--mockup img {
    left: unset;
    width: 100%;
    max-width: 732px;
    height: auto;
    position: absolute;
    top: 50%;
    right: -112px;
    transform: translateY(-50%);
  }
}

@media screen and (width >= 1100px) {
  .hero--heading {
    letter-spacing: -.0625rem;
    font-size: 3.5rem;
    line-height: 4rem;
  }

  .hero--description {
    letter-spacing: -.0175rem;
    margin-top: 1.5rem;
    font-size: 1.125rem;
    line-height: 1.75rem;
  }
}
